<template>
  <div class="cart__block">
    <ul class="cart__orders">
      <li class="cart__order" v-for="cartItem in cartItems" :key="cartItem?.product.id">
        <h3>{{cartItem?.product.title +
          ((cartItem.quantity > 1) ? ` (${cartItem.quantity} шт.)` : '')}}</h3>
        <b>{{this.$filters.currencyRUB(cartItem?.product.price)}}</b>
        <span>Артикул: {{this.$filters.vendorNumber(cartItem?.product.id)}}</span>
      </li>
    </ul>

    <div class="cart__total">
      <p>Доставка: <b>500 ₽</b></p>
      <p>Итого: <b>{{itemsCount}}</b>
      {{this.$filters.countText(itemsCount)}} на сумму <b>
      {{this.$filters.currencyRUB(totalPrice)}}</b></p>
    </div>

    <slot/>
  </div>
</template>
<script>

export default {

  props: ['cartItems', 'totalPrice'],
  computed: {
    itemsCount() {
      return this.cartItems.reduce((acc, item) => item.quantity + acc, 0);
    },
  },
};
</script>
