<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable max-len -->
<template>
  <aside class="filter">
      <h2 class="filter__title">Фильтры</h2>

      <form class="filter__form form" action="#" method="get" @submit.prevent="submit()">
        <fieldset class="form__block">
          <legend class="form__legend">Цена</legend>
          <label class="form__label form__label--price" for="min-price">
            <input class="form__input" type="number" min="0" step="1" name="min-price" v-model="currentPriceFrom">
            <span class="form__value">От</span>
          </label>
          <label class="form__label form__label--price">
            <input class="form__input" type="number" min="0" step="1" max="1000000" name="max-price" v-model="currentPriceTo">
            <span class="form__value">До</span>
          </label>
        </fieldset>

        <fieldset class="form__block">
          <legend class="form__legend">Категория</legend>
          <label class="form__label form__label--select">
            <select class="form__select" type="text" name="category" v-model="currentCategoryId">
              <option value="0">Все категории</option>
              <option :value="category.id" v-for="category in categories" :key="category.id">{{category.title}}</option>
            </select>
          </label>
        </fieldset>

        <fieldset class="form__block">
          <legend class="form__legend">Цвет</legend>
          <ul class="colors">
            <li class="colors__item" v-for="color in colors" :key="color.id">
              <label class="colors__label">
                <input class="colors__radio sr-only" type="radio" name="color"
                :value="color.id" v-model="currentColorId">
                <span class="colors__value" :style="`background: ${color.code}`"></span>
              </label>
            </li>
          </ul>
        </fieldset>

        <fieldset class="form__block">
          <legend class="form__legend">Количество струн</legend>
          <ul class="check-list">
            <li class="check-list__item">
              <label class="check-list__label">
                <input class="check-list__check sr-only" type="checkbox" name="volume" value="4">
                <span class="check-list__desc">4<span>(254)</span>
                </span>
              </label>
            </li>
            <li class="check-list__item">
              <label class="check-list__label">
                <input class="check-list__check sr-only" type="checkbox" name="volume" value="5">
                <span class="check-list__desc">5<span>(461)</span>
                </span>
              </label>
            </li>
            <li class="check-list__item">
              <label class="check-list__label">
                <input class="check-list__check sr-only" type="checkbox" name="volume" value="6" checked>
                <span class="check-list__desc">6<span>(713)</span>
                </span>
              </label>
            </li>
            <li class="check-list__item">
              <label class="check-list__label">
                <input class="check-list__check sr-only" type="checkbox" name="volume" value="7">
                <span class="check-list__desc">7<span>(23)</span>
                </span>
              </label>
            </li>
            <li class="check-list__item">
              <label class="check-list__label">
                <input class="check-list__check sr-only" type="checkbox" name="volume" value="8">
                <span class="check-list__desc">8<span>(10)</span>
                </span>
              </label>
            </li>
            <li class="check-list__item">
              <label class="check-list__label">
                <input class="check-list__check sr-only" type="checkbox" name="volume" value="12">
                <span class="check-list__desc">12<span>(2)</span>
                </span>
              </label>
            </li>
          </ul>
        </fieldset>

        <button class="filter__submit button button--primery" type="submit">
          Применить
        </button>
        <button class="filter__reset button button--second" type="button" @click="reset()">
          Сбросить
        </button>
      </form>
    </aside>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColorId: 0,
      colorsData: null,
      categoriesData: null,
    };
  },
  props: ['priceFrom', 'priceTo', 'categoryId', 'colorId'],

  computed: {
    categories() {
      return this.categoriesData
        ? this.categoriesData.items
        : [];
    },
    colors() {
      return this.colorsData
      // удаляем некорректный элемент, возвращаемый API
        ? this.colorsData.items.slice(0, -1)
        : [];
    },
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategoryId = value;
    },
    colorId(value) {
      this.currentColorId = value;
      console.log(value);
    },
  },
  methods: {
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categoryId', this.currentCategoryId);
      this.$emit('update:colorId', this.currentColorId);
    },
    reset() {
      this.$emit('update:priceFrom', 0);
      this.$emit('update:priceTo', 0);
      this.$emit('update:categoryId', 0);
      this.$emit('update:colorId', 0);
    },

    loadCategories() {
      axios.get('https://vue-study.skillbox.cc/api/productCategories')
        .then((response) => { (this.categoriesData = response.data); });
    },
    loadColors() {
      axios.get('https://vue-study.skillbox.cc/api/colors')
        .then((response) => { (this.colorsData = response.data); });
    },
  },
  created() {
    this.loadCategories();
    this.loadColors();
  },
};

</script>
