<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info">{{countString}}</span>
    </div>

    <div class="content__catalog">
      <product-filter
      :price-from="filterPriceFrom" @update:priceFrom="priceFromUpdate($event)"
      :price-to="filterPriceTo" @update:priceTo="priceToUpdate($event)"
      :category-id="filterCategoryId" @update:categoryId="categoryIdUpdate($event)"
      :colorId="filterColorId" @update:colorId="colorIdUpdate($event)"/>
      <section class="catalogApp">

        <loading-spinner :loading="productsLoading" :loadingFailed="productsLoadingFailing"/>
        <product-list :products="products"/>

        <app-pagination :page="this.currentPage"
        :count="countProducts"
        :per-page="productsPerPage"
        @pagination="onPagination($event)"/>
      </section>
    </div>
  </main>
</template>

<script>

import BASE_API_URL from '@/config';
import axios from 'axios';
import LoadingSpinner from '@/components/LoadingSpinner.vue';
import ProductList from '../components/ProductList.vue';
import AppPagination from '../components/AppPagination.vue';
import ProductFilter from '../components/ProductFilter.vue';

export default {
  components: {
    ProductList, AppPagination, ProductFilter, LoadingSpinner,
  },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,
      currentPage: 1,
      productsPerPage: 6,
      productsData: null,
      productsLoading: false,
      productsLoadingFailing: false,
    };
  },
  computed: {
    // общее количество товаров
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },

    // возвращает фрагмент массива товаров
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => ({ ...product, image: product.image.file.url }))
        : [];
    },
    // возвращает строку вида 'N товар/а/ов'
    countString() {
      return this.$filters.countItemsText(this.countProducts);
    },
  },
  methods: {
    // изменяет номер текущей страницы
    onPagination(page) {
      this.currentPage = page;
    },

    priceFromUpdate(event) {
      this.filterPriceFrom = event;
      console.log(`priceFromUpdated: ${event}`);
    },
    priceToUpdate(event) {
      this.filterPriceTo = event;
      console.log(`priceToUpdated: ${event}`);
    },
    categoryIdUpdate(event) {
      this.filterCategoryId = event;
      console.log(`categoryIdUpdated: ${event}`);
    },
    colorIdUpdate(event) {
      this.filterColorId = event;
      console.log(`colorIdUpdated: ${event}`);
    },
    loadProducts() {
      const url = `${BASE_API_URL}/products`;
      clearTimeout(this.productsTimer);
      this.productsLoading = true;
      this.productsLoadingFailing = false;
      this.productsTimer = setTimeout(() => axios.get(url, {
        params: {
          page: this.currentPage,
          limit: this.productsPerPage,
          categoryId: this.filterCategoryId,
          minPrice: this.filterPriceFrom,
          maxPrice: this.filterPriceTo,
          colorId: this.filterColorId,
        },
      })

        .then((response) => { this.productsData = response.data; })
        .catch(() => { this.productsLoadingFailing = true; })
        .then(() => { this.productsLoading = false; }), 3000);
    },
  },
  watch: {
    currentPage() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
