<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'order'}">
            Оформление заказа
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{this.$filters.countItemsText(itemsCount)}}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <base-form-text title="ФИО"  v-model="formData.name"
            :error="formError.name" placeholder="Введите ваше полное имя"/>

            <base-form-text title="Адрес доставки" v-model="formData.address"
            :error="formError.address" placeholder="Введите ваш адрес"/>

            <base-form-text title="Телефон" v-model="formData.phone"
            :error="formError.phone" placeholder="Введите ваш телефон"/>

            <base-form-text title="Email" v-model="formData.email"
            :error="formError.email" placeholder="Введите ваш Email"/>

            <base-form-textarea title="Комментарий к заказу" v-model="formData.comments"
            :error="formError.comments" placeholder="Ваши пожелания"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio"
                  name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <cart-block :cartItems="cartItems" :totalPrice="totalPrice">
          <button class="cart__button button button--primery" type="submit">
            <button-loader v-if="formSending"/>
            <template v-else>Оформить заказ</template>
          </button>
        </cart-block>
        <div class="cart__error form__error-block" v-if="this.formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ this.formErrorMessage}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>
<script>

import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import CartBlock from '@/components/CartBlock.vue';
import ButtonLoader from '@/components/ButtonLoader.vue';
import axios from 'axios';
import { mapGetters } from 'vuex';
import BASE_API_URL from '@/config';

export default {
  components: {
    BaseFormText, BaseFormTextarea, CartBlock, ButtonLoader,
  },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      formSending: false,
    };
  },
  methods: {
    order() {
      this.formError = {};
      this.formSending = true;
      clearTimeout(this.ordersTimer);
      this.productTimer = setTimeout(() => axios.post(`${BASE_API_URL}/orders`, { ...this.formData }, {
        params: { userAccessKey: this.$store.state.userAccessKey },
      })
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response?.data);
          console.log(response);
          this.$router.push({ name: 'orderInfo', params: { id: response?.data?.id } });
        })
        .catch((error) => {
          console.log(error);
          this.formError = error?.response?.data?.error?.request || {};
          this.formErrorMessage = error?.response?.data?.error?.message;
        }).then(() => {
          this.formSending = false;
        }), 1500);
    },
  },
  computed: {
    ...mapGetters({
      cartItems: 'cartDetailsProducts',
      totalPrice: 'cartTotalPrice',
      itemsCount: 'cartItemsCount',
    }),
  },
};
</script>
