<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable max-len -->
<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">
      {{ $filters.vendorNumber(item.product.id) }}
    </span>

    <product-counter :count="amount" @update:countChange="onCountChange($event)"/>

    <b class="product__price">
      {{ $filters.currencyRUB(item.amount * item.product.price) }}
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
    @click.prevent="deleteProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>
<script>
import { mapActions } from 'vuex';
import ProductCounter from './ProductCounter.vue';

export default {
  components: { ProductCounter },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        const payload = {
          productId: this.item.productId,
          amount: value,
        };
        this.$store.dispatch('updateCartProductsAmount', payload);
      },
    },
  },
  methods: {
    ...mapActions({ deleteProduct: 'deleteProductFromCart' }),
    onCountChange(event) {
      this.amount = parseInt(event, 10);
    },
  },
};
</script>
