<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <div :class="classForm">
    <button type="button" aria-label="Убрать один товар" @click.prevent="counterDecrement()">
      <svg width="10" height="10" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model="counterValue" @change="changeCount" name="count">

    <button type="button" aria-label="Добавить один товар" @click.prevent="counterIncrement()">
      <svg width="10" height="10" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>
<script>
export default {
  props: ['count'],
  data() {
    return {
      counterValue: this.count,
    };
  },
  methods: {
    counterIncrement() {
      this.counterValue += 1;
      this.$emit('update:countChange', this.counterValue);
    },
    counterDecrement() {
      if (this.counterValue > 1) {
        this.counterValue -= 1;
        this.$emit('update:countChange', this.counterValue);
      }
    },
    changeCount(e) {
      this.counterValue = e.target.value;
      this.$emit('update:countChange', this.counterValue);
    },
  },
  watch: {
    count(value) {
      this.counterValue = value;
    },
  },
  computed: {
    classForm() {
      const classArray = ['form__counter'];
      if (this.$route.name === 'cart') classArray.splice(0, 0, ['product__counter']);
      return classArray;
    },
  },
};
</script>
