import { createStore } from 'vuex';
import BASE_API_URL from '@/config';
import axios from 'axios';

export default createStore({
  state() {
    return {
      basketsUrl: `${BASE_API_URL}/baskets`,
      ordersUrl: `${BASE_API_URL}/orders/`,
      cartProducts: [],
      userAccessKey: null,
      cartProductsData: [],
      orderInfo: null,
    };
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateProductAmount(state, payload) {
      const updatedItem = state.cartProducts.find((item) => item.productId === payload.productId);
      if (updatedItem) {
        updatedItem.amount = payload.amount;
      }
    },

    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },

    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
      }));
    },
  },
  getters: {
    getOrderInfo(state) {
      return state.orderInfo;
    },
    cartDetailsProducts(state) {
      return state.cartProducts.map((item) => {
        const { product } = state.cartProductsData.find((p) => p.product.id === item.productId);

        return {
          ...item,
          quantity: item.amount,
          product: {
            ...product,
            image: product.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailsProducts
        .reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    },
    cartItemsCount(state, getters) {
      return getters.cartDetailsProducts.reduce((acc, item) => item.amount + acc, 0);
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios.get(this.state.ordersUrl + orderId, {
        params: { userAccessKey: context.state.userAccessKey },
      }).then((response) => {
        context.commit('updateOrderInfo', response?.data);
      });
    },
    loadCart(context) {
      return axios.get(this.state.basketsUrl, {
        params: { userAccessKey: context.state.userAccessKey },
      }).then((response) => {
        if (!context.state.userAccessKey) {
          localStorage.setItem('userAccessKey', response.data.user.accessKey);
          context.commit('updateUserAccessKey', response.data.user.accessKey);
        }
        context.commit('updateCartProductsData', response.data.items);
        context.commit('syncCartProducts');
      });
    },
    addProductToCart(context, productItem) {
      // eslint-disable-next-line no-promise-executor-return
      return (new Promise((resolve) => setTimeout(resolve, 2000)))
        .then(() => {
          axios.post(`${this.state.basketsUrl}/products`, productItem, {
            params: { userAccessKey: context.state.userAccessKey },
          }).then((response) => {
            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProducts');
          });
        });
    },

    updateCartProductsAmount(context, productItem) {
      context.commit('updateProductAmount', productItem);
      // eslint-disable-next-line no-promise-executor-return
      return (new Promise((resolve) => setTimeout(resolve, 2000)))
        .then(() => {
          axios.put(`${this.state.basketsUrl}/products`, {
            productId: productItem.productId,
            quantity: productItem.amount,
          }, {
            params: { userAccessKey: context.state.userAccessKey },
          }).then((response) => {
            context.commit('updateCartProductsData', response.data.items);
          }).catch(() => {
            context.commit('syncCartProducts');
          });
        });
    },

    deleteProductFromCart(context, productId) {
      // eslint-disable-next-line no-promise-executor-return
      return (new Promise((resolve) => setTimeout(resolve, 2000)))
        .then(() => {
          axios.delete(`${this.state.basketsUrl}/products`, {
            data: { productId },
            params: { userAccessKey: context.state.userAccessKey },
          }).then((response) => {
            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProducts');
          });
        });
    },
  },
});
