<!-- eslint-disable -->
<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow " :class="{'pagination__link--disabled' : !isPrevBtnEnabled}" aria-label="Предыдущая страница" @click.prevent="pagination('prev')">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>

    <li class="pagination__item" v-for="pageNumber in pageCount" :key="pageNumber">
      <a class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" @click.prevent="pagination(pageNumber)">
        {{pageNumber}}
      </a>
    </li>

    <li class="pagination__item" >
      <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled' : !isNextBtnEnabled}" href="#" aria-label="Следующая страница" @click.prevent="pagination('next')">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  props: ['page', 'count', 'perPage'],
  computed: {
    pageCount() {
      return Math.ceil(this.count / this.perPage);
    },
    isNextBtnEnabled() {
      return (this.page < this.pageCount);
    },
    isPrevBtnEnabled() {
      return (this.page > 1);
    },
  },
  methods: {
    pagination(arg) {
      let nextPage;
      if (arg === 'next') {
        if (this.page === this.pageCount) return;
        nextPage = this.page + 1;
      } else if (arg === 'prev') {
        if (this.page === 1) return;
        nextPage = this.page - 1;
      } else {
        nextPage = arg;
      }
      this.$emit('pagination', nextPage);
    },
  },
};
</script>
